/* Default styling for larger screens */
.react-activity-calendar {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: var(--FF);
	background-color: var(--BG-COLOR);
	min-height: 100vh;
	color: #fff;
	padding: 80px 60px 0px 60px;
  }
  .gitcontainer img{
width: 100%;
  }
  .react-activity-calendar h1 {
	font-weight: 600;
	font-size: 2.2rem;
	color: #6abdf9;
	font-family: var(--FF-1);
	padding-bottom: 10px;
  }
  
  .react-activity-calendar h1::after {
	content: "";
	height: 1px;
	width: 100%;
	background-color: #6abdf9;
	display: inline-block;
	box-shadow: 0px 5px 10px #6abdf97c;
  }
  
  .gridcontainer {
	display: flex;
	flex-wrap: wrap;
	gap: 20px;
	margin: 20px 0;
	justify-content: center; /* Center the divs horizontally */
	text-align: center; /* Center the content inside divs */
  }
  
  .gridcontainer img {
	max-width: 100%; /* Ensure images fit within smaller screens */
	height: auto;
	display: block; /* Center the images within their divs */
  }
  
  .calender {
	max-width: 100%;
	height: auto;
	align-items: center;
	margin-bottom: 40px;
  }
  
  /* Media query for smaller screens */
  @media (max-width: 700px) {
	.react-activity-calendar {
	  padding: 40px 30px 0px 30px;
	}
  
	.react-activity-calendar h1 {
	  font-size: 1.8rem;
	}
  }
  
  /* Media query for even smaller screens */
  @media (max-width: 480px) {
	.react-activity-calendar {
	  padding: 30px 20px 0px 20px;
	}
  
	.react-activity-calendar h1 {
	  font-size: 1.6rem;
	}
  }
    
@media screen and (min-width: 701px) and (max-width: 1023px){
	.react-activity-calendar {
	  
		
	   margin-left: 15%;
	   width: 105vw;
	 
	  }
    
	
  }
  