/* Default styling for larger screens */

.project {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: var(--FF);
    background-color: var(--BG-COLOR);
    min-height: 100vh;
    color: #fff;
    padding: 150px 10px 0px 10px;
  
  }
 
  .project h1 {
    font-weight: 600;
    font-size: 2.2rem;
    color: #6abdf9;
    font-family: var(--FF-1);
    padding-bottom: 10px;
  }
  
  .project h1::after {
    content: "";
    height: 1px;
    width: 100%;
    background-color: #6abdf9;
    display: inline-block;
    box-shadow: 0px 5px 10px #6abdf97c;
  }
  
  .container-card {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 25px;
    justify-content: center; /* Center the grid horizontally */
    padding: 100px 30px; /* Adjust padding */
    padding-top: 60px;
  }
  
  .project-card {
    border-radius: 10px;
    width: 100%; /* Make cards take full width on smaller screens */
    padding: 20px;
    background-color: #373a3c7c;
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  }
  
  .project-card h2 {
    color: #6abdf9;
    padding-bottom: 10px;
    padding-top: 10px;

    
  }
  
  .upper h2 {
    display: flex;
    flex-direction: column;
    align-items: center; /* Center content vertically and horizontally */
  }
  
  .upper img {
    /* width: 100%;
    max-height: 310px; */
    object-fit: contain;
  }
  
  .project-card h4 {
    color: rgb(173, 177, 177);
    padding-bottom: 10px;
  }
  
  .lower {
    display: flex;
    width: 100%;
    gap: 20px;
    justify-content: space-between; /* Space out the links */
    padding-top: 10px;
  }
  
  .lower a {
    text-decoration: none;
  }
  
  .project-github-link
 {
    display: flex;
    border: 0.5px solid white;
    width: 130px;
    height: 50px;
    border-radius: 30px;
    padding: 15px;
    color: white;
    gap: 15px;
    background-color: var(--BLUE-TEXT);
  }
  .project-deployed-link {
    display: flex;
    border: 0.5px solid white;
    width: 190px;
    height: 50px;
    border-radius: 30px;
    padding: 15px;
    color: white;
    gap: 15px;
    background-color: var(--BLUE-TEXT);
  }
  .stack{
    align-items: center;
  }
  a {
    color: white;
  }
  
  .stacks{
    display: flex;
   
    width: auto;
  }
  /* Media query for smaller screens */
  @media screen and (max-width: 700px) {
    .container-card {
      grid-template-columns: repeat(1, 1fr); /* Display one project per row on smaller screens */
      padding: 120px 20px; /* Adjust padding */
    }
  
    .project-card {
      width: 100%;
      margin: auto;
    }
    .project-card:first-child .upper h2.project-title {
        font-size: 1.3rem; /* Adjust the font size as needed */
      }
      .stacks{
        width:120%;
        
        
      }
      .stacks h4 {
        font-size: 0.9rem; 
        
        
      }
      .stacks p {
        font-size: 0.8rem; 
        
        
      }
    
  }
  
  
@media screen and (min-width: 701px) and (max-width: 1023px){
	.project {
	  
		
	   margin-left: 15%;
	   width: 105vw;
	 
	  }
