.skills {
	margin: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: var(--FF);
	background-color: var(--BG-COLOR);
	
	min-height: 100vh;
	color: #fff;
	padding: 80px 60px 0px 60px;
	
	
}

.skills h1 {
	font-weight: 300;
	font-size: 2.2rem;
	color: #6abdf9;
    font-family: var(--FF-1);
    padding-bottom: 10px;
}
.skills h1::after {
    content: "";
    height: 1px;
    width: 100%;
    background-color: #6abdf9;
    display: inline-block;
    box-shadow: 0px 5px 10px #6abdf97c;
}
.container {
	display: flex;
	transition: 0.5s;
	padding-top: 60px;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;

}
.container .skills-card {
	width: 300px;
	height: 400px;
	display: flex;
	justify-content: center;
	transition: 0.5s;
}
.card {
	position: relative;
	background-color: var(--BG-COLOR-1);
	display: flex;
	flex-direction: column;
	align-items: center;
	border: 2px solid var(--BLUE-TEXT);
	cursor: pointer;
	transition: all 0.5s;
	height: 250px;
	width: 200px;
	box-shadow: 0px 0px 10px rgba(8, 114, 191);
}
.card .image-div {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	transition: 0.5s;
}
.card .image-div h2 {
	font-weight: 300;
	margin-top: 20px;
	transition: 0.5s;
}
.card .image-div img {
	width: 100px;
	transition: 0.5s;
}
.card:hover {
	height: 380px;
	width: 300px;
	transition: 0.5s;
	box-shadow: 0px 0px 30px red;
	border-color: red;
}
.card:hover .image-div {
	top: 20px;
	transform: translate(0);
}
.card:hover .description {
	visibility: visible;
	animation: text-animation 1s 0.5s forwards;
}
.card .description {
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	color: #fff;
	font-weight: 300;
	font-style: italic;
	padding: 20px;
	height: 200px;
	position: absolute;
	opacity: 0;
	visibility: none;
	top: 180px;
}
 .more_skills {
	height: 60px;
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 20px;
}
 .more_skills .more_skill {
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: hsla(0, 0%, 30%, 0.267);
	border-radius: 12px;
	cursor: pointer;
	transition: 0.2s;
	animation: bounce-in-top 1.1s both;
	position: relative;
}
 .more_skills .more_skill:focus {
	-webkit-animation: bounce-in-top 0.9s both;
	animation: bounce-in-top 0.9s both;
}
 .more_skills .more_skill:active {
	-webkit-animation: none;
	animation: none;
}

 .more_skills .more_skill img {
	width: 30px;
}

 .more_skills .more_skill:hover {
	scale: 1.3;
	transition: 0.2s;
	border: 1px var(--BLUE-TEXT) solid;
}
.more_skill::after {
	display: block;
	position: absolute;
	top: 60px;
	text-align: center;
	margin: 0px;
	padding: 0px;
	transition: 0.5s;
	font-size: 0.6rem;
}
.more_skill:hover:nth-child(1)::after {
	content: "VsCode"
}
.more_skill:hover:nth-child(2):after {
	content: "PostMan"
}
.more_skill:hover:nth-child(3):after {
	content: "GIT"
}
.more_skill:hover:nth-child(4):after {
	content: "Github"
}
.more_skill:hover:nth-child(5):after {
	content: "Canva"
}
/* .more_skill:hover:nth-child(6):after {
	content: "Node Package Modules"
} */
.more_skill:hover:nth-child(6):after {
	content: "Canva"
}

/* .more_skill:hover:nth-child(9):after {
	content: "Figma"
} */
.tools h1{
	text-align: center;
}

@media screen and (max-width: 700px) {
	.skills {
	  
	  margin:auto
	  
	 
	
	}
  
	.more_skill::after {
	  display: none;
	}


	.container {
		
	  display: flex;
	  flex-wrap: wrap;
	  justify-content: center; /* Center the columns horizontally */
    align-items: center;; /* Center the columns horizontally */
	}
  
	.container .skills-card {
		flex: 0 0 calc(50% - 20px); /* Adjust the width to fit two skills per column */
		margin: 10px; /* Add margin for spacing between skills cards */
	  }
  
	 .more_skills {
	  max-width: 250px;
	  column-gap: 20px;
	  row-gap: 30px;
	  height: 200px;
	}
  }
  @media screen and (min-width: 701px) and (max-width: 1023px){
	.skills {
	  
		
	   margin-left: 14%;
	   width: 105vw;
	  
	  }
	  .skills-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr); /* Two skills per row */
        justify-content: center; /* Center the skills horizontally */
        align-items: center; /* Center the skills vertically */
        margin-left: 100px;
        margin-bottom: 20px;
    }
  }
  
@keyframes text-animation {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}


@-webkit-keyframes bounce-in-top {
	0% {
		-webkit-transform: translateY(-500px);
		transform: translateY(-500px);
		-webkit-animation-timing-function: ease-in;
		animation-timing-function: ease-in;
		opacity: 0;
	}
	38% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
		-webkit-animation-timing-function: ease-out;
		animation-timing-function: ease-out;
		opacity: 1;
	}
	55% {
		-webkit-transform: translateY(-65px);
		transform: translateY(-65px);
		-webkit-animation-timing-function: ease-in;
		animation-timing-function: ease-in;
	}
	72% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
		-webkit-animation-timing-function: ease-out;
		animation-timing-function: ease-out;
	}
	81% {
		-webkit-transform: translateY(-28px);
		transform: translateY(-28px);
		-webkit-animation-timing-function: ease-in;
		animation-timing-function: ease-in;
	}
	90% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
		-webkit-animation-timing-function: ease-out;
		animation-timing-function: ease-out;
	}
	95% {
		-webkit-transform: translateY(-8px);
		transform: translateY(-8px);
		-webkit-animation-timing-function: ease-in;
		animation-timing-function: ease-in;
	}
	100% {
		-webkit-transform: translateY(0);
		transform: translateY(0);
		-webkit-animation-timing-function: ease-out;
		animation-timing-function: ease-out;
	}
}

