.footer {
    background-color: var(--BG-COLOR);
    height: 180px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: var(--FF);
    row-gap: 30px;
    padding: 20px 0px;

}
.footer::before {
    content: "";
    display: inline-block;
    height: 1px;
    width: 90%;
    background-color: var(--BLUE-TEXT);
}
.links {
    display: flex;
    column-gap: 30px;
}
.link a {
    display: flex;
    column-gap: 20px;
    color: #fff;
    text-decoration: none;

}
.link a img {
    width: 20px;
}
.link a p {
    display: inline-block;
    font-size: 1rem;
}
.socials {
    display: flex;
    column-gap: 20px;
    justify-content: center;
    align-items: center;
}
.socials a {
    background-color: #fff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px #fff solid;
}
.socials a img {
    width: 30px;
}

.email {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    gap: 20px;
    position: relative;
    transition: 0.5s;
}

.email img {
    width: 30px;
    background-color: #fff;
    border-radius: 50%;
    border: 1px solid #fff;
    position: relative;
    cursor: pointer;
}

.email p {
    margin-top: 10px; /* Adjust the spacing as needed */
    opacity: 0;
    transition: 0.5s;
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    top: 100%; /* This makes the text appear below the image */
}
.email:hover p {
    opacity: 1;
    
}

@media screen and (max-width: 700px) {
    .email {
        display: none;
    }
}
@media screen and (min-width: 701px) and (max-width: 1023px){
	.footer {

		
	   margin-left: 15%;
	   width: 105vw;
	 
