.about {
  background-color: var(--BG-COLOR);
  color: #fff;
  font-family: var(--FF);
  padding-top: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
 
 
}
#maincont{
  display: grid;
  grid-template-columns: repeat(2,1fr);
 gap: 10px;
  /* flex-direction: column; */
  /* justify-content: space-between; */
  /* align-items: center; */
}
/* p{
  margin-left: 280px;
} */

.about h1 {
  font-weight: 300;
  font-size: 2.2rem;
  color: var(--BLUE-TEXT-LIGHT);
  font-family: var(--FF-1);
  padding-bottom: 10px;
  margin-bottom: 30px;
}
.about h1::after {
  content: "";
  height: 1px;
  width: 100%;
  background-color: var(--BLUE-TEXT-LIGHT);
  display: inline-block;
  box-shadow: 0px 5px 10px #6abdf97c;
}
.about p {
  font-weight: 300;
  font-size: 1.2rem;
  font-style: italic;
  max-width: 600px;
  text-align: center;
  line-height: 2rem;
}

.favourite-podcasts {
  margin-top: 80px;
}
.favourite-podcasts h3 {
  font-weight: 300;
  font-size: 1.3rem;
  display: inline-block;
  color: var(--BLUE-TEXT-LIGHT);
}
.favourite-podcasts h3::after {
  content: "";
  display: inline-block;
  height: 0.5px;
  width: 100%;
  margin-bottom: 10px;
  background-color: var(--BLUE-TEXT-LIGHT);
  box-shadow: 0px 1px 10px var(--BLUE-TEXT-LIGHT);
}
.favourite-podcasts .container {
  display: flex;
  column-gap: 30px;
  justify-content: center;
  flex-wrap: wrap;
  padding-top: 10px;
}
.container .podcast {
  width: 150px;
  height: 150px;
  transition: 0.5s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.podcast a img {
  width: 100px;
  transition: 0.5s;
  border: 1px solid var(--BLUE-TEXT-LIGHT);
  padding: 3px;
}
.podcast:hover a img {
  transform: scale(1.4);
  box-shadow: 0px 0px 7px var(--BLUE-TEXT-LIGHT);
}

@media screen and (max-width: 700px) {
  .favourite-podcasts h3 {
    margin-left: 20px;
  }
  .about p {
    max-width: 300px;
    font-size: 1rem;
  }
  #maincont{
    display: grid;
    grid-template-columns: repeat(1,1fr);
   margin-bottom: 140px;
  }
}

@media screen and (min-width: 701px) and (max-width: 1023px){
	#about {
	  
		
	   margin-left: 16%;
	   width: 105vw;
	 
	  }
	
  }

